import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';

const styles = theme => ({
    root: {
      width: '90%',
    },
    
  });
class Benefits extends React.Component {
    render() {
        const { classes } = this.props;
        return (
        <Paper className={classes.paper}>
            <Typography component={'span'} variant="h7" >
            Benefits of using Motocoach Direct
            <ul>
               <li>Get wholesale level pricing on the Coach of your choice not available to retail consumers</li>
               <li>Never Step foot in a dealership</li>
               <li>Never haggle with a salesman</li>
               <li>Have every detail of your purchase attended to by oine of our specialists</li>
            </ul>
            </Typography>
        </Paper>
        );
    }
}
Benefits.propTypes = {
    classes: PropTypes.object,
};
  
export default withStyles(styles)(Benefits);